
import StateJS from './../js/state.js'


const use_root = { root: true }




function makeOpenWithList(srclist) {
    if (srclist == null) { srclist = [] }
    const valueForItem = function (item) {
        if (item == null) { return 0 }
        let v = item.open_with_priority;
        if (v == null) { return 0 }
        return v;
    }
    srclist.sort((a, b) => {
        let v1 = valueForItem(a)
        let v2 = valueForItem(b)
        return (v2 - v1);
    })
    let dstlist = []
    for (var i in srclist) {
        let item = srclist[i]
        let value = valueForItem(item)
        if (value > 0) {
            dstlist.push(item)
        } else {
            break
        }
    }
    return dstlist
}



////////////////////////////////////////////////////////////////////////////////

function Filter() {
    this.text = "";
}


Filter.prototype = {

    containString(keyword, text) {
        if ((keyword == null) || (text == null)) { return false }

        keyword = this.normalizeText(keyword)
        text = this.normalizeText(text)

        let i = text.indexOf(keyword)
        return (i >= 0)
    },

    normalizeText(text) {
        if (text == "") { return "" }
        return text.trim().toLowerCase()
    },

    prepare(text) {
        this.text = text;
    },

    accept(item) {
        let filter = this.text;
        if (this.containString(filter, item.name)) { return true }
        if (this.containString(filter, item.title)) { return true }
        if (this.containString(filter, item.description)) { return true }
        // if (this.containString(filter, item.path)) { return true }
        return false;
    },
}


////////////////////////////////////////////////////////////////////////////////



const state = {
    executables: [],
    openWithList: [],
};


const filter_obj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "executables" })
state_rw.init(state)
state_rw.setFilter(state, { filter: filter_obj })
state_rw.setPage(state, { limit: 10, offset: 0 })


const getters = {

    openWithList(state) {
        return state.openWithList
    },

    all(state) {
        return state_rw.listAll(state)
    },

    currentPageItems(state) {
        return state_rw.listCurrentPage(state)
    },

    currentPage(state) {
        return state_rw.currentPageNumber(state)
    },

    pageSize(state) {
        return state_rw.limit(state)
    },

    afterFilter(state) {
        return state_rw.listFilterResults(state)
    },

    total(state) {
        return state_rw.total(state)
    },
}

const mutations = {

    removeFromState(state, id) {
        state_rw.remove(state, id)
    },

    update(state, p) {
        let vo = p.vo;
        let limit = p.size;
        let index = p.index;
        let text = p.filter;

        if (vo != null) {
            state_rw.updateVO(state, vo)
        }

        if (limit != null || index != null) {
            state_rw.setPage(state, { limit, index })
        }

        if (text != null) {
            state_rw.setFilter(state, { text })
        }

        let src = state_rw.listAll(state);
        let dst = makeOpenWithList(src)
        state.openWithList = dst
    },

};

const actions = {

    init(context) {
        context.dispatch("fetch")
    },

    fetch(context) {
        let method = 'GET'
        let type = 'executables'
        let p = { method, type }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('update', { vo })
            })
    },

    insert(context, item) {
        let method = 'POST'
        let type = 'executables'
        let data = { executables: [item] }
        let p = { method, type, data }
        return context.dispatch("rest/execute", p, use_root)
            .then(() => {
                context.dispatch('fetch')
            })
    },

    update(context, item) {
        let method = 'PUT'
        let type = 'executables'
        let data = { executables: [item] }
        let id = item.id;
        let p = { method, type, id, data }
        return context.dispatch("rest/execute", p, use_root)
            .then(() => {
                context.dispatch('fetch')
            })
    },

    prepareInsert(context, item) {
        let method = 'POST'
        let type = 'import-executables'
        let data = { executables: [item] }
        let p = { method, type, data }
        return context.dispatch("rest/execute", p, use_root)
    },

    remove(context, id) {
        let method = 'DELETE'
        let type = 'executables'
        let data = {};
        let p = { method, type, id, data }
        return context.dispatch("rest/execute", p, use_root)
            .then(() => {
                context.dispatch('fetch')
            })
    }
};


export default {
    name: "module_executable",
    namespaced: true,
    state, mutations, actions, getters
}
